@use 'abstracts/mixins' as *;

.jumbotron {
  padding-top: 166px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--white);
    font-size: 20px;
    margin-bottom: 23px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 21px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--white);
    font-weight: 300;
    margin-bottom: 52px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
      margin-bottom: 112px;
      padding-right: 10px;
    }

    span {
      color: var(--gold);
    }
  }

  &__description {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 55px;
    padding-right: 179px;

    @include md {
      padding-right: 9px;
      padding-bottom: 46px;
    }

    p {
      position: relative;
      max-width: 640px;
      width: 100%;
      color: var(--white);
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 1.2px;
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);

      @include md {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.8px;
        width: 100%;
        padding-left: 63px;
        padding-right: 5px;
      }

      &::before {
        content: '';
        display: block;
        background-color: var(--gold);
        width: 2px;
        margin-top: 2px;
        height: 770px;
        position: absolute;
        top: 0;
        left: -60px;

        @include md {
          height: 616px;
          left: 39px;
        }
      }
    }
  }

  &__parallax {
    background: url('./../assets/about-us-parallax-bg.webp');
    min-height: 400px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include md {
      min-height: 340px;
      background-attachment: scroll !important;
      background-position: center !important;
    }
  }
}

.intro {
  padding-top: 100px;

  @include md {
    padding-top: 44px;
  }

  .container {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }

  &__text {
    flex-shrink: 0;
    margin-right: 179px;
    max-width: 640px;
    padding-right: 12px;
    padding-left: 3px;
    width: 70%;
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: 300;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    margin-bottom: 28px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.4px;
      width: 100%;
      padding-left: 63px;
      margin-right: 0;
      padding-right: 5px;
      margin-bottom: 22px;
    }
  }
}

.why {
  padding-top: 30px;
  padding-bottom: 83px;

  @include md {
    padding-top: 43px;
  }

  .desktop-container {
    padding: 0 30px;
    width: 100%;
    max-width: 1190px;
    margin: 0 auto;

    @include md {
      padding: 0;
    }
  }

  &__video {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 56.25%;
    margin-top: 24px;

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.who {
  padding-bottom: 73px;

  .about-us__title {
    margin-bottom: 16px;

    @include md {
      margin-bottom: 40px;
    }
  }

  &__item {
    background: var(--light-grey);
    padding: 38px 218px 39px 68px;
    margin-bottom: 11px;
    max-width: 842px;

    @include md {
      padding: 37px 20px;
    }

    &__title {
      color: var(--mid-green);
      font-size: 22px;
      letter-spacing: 2.2px;
      margin-bottom: 19px;
      text-transform: uppercase;

      @include md {
        font-size: 16px;
        line-height: 21px;
        letter-spacing: 1.6px;
        margin-bottom: 13px;
      }
    }

    &__list {
      padding-left: 33px;

      @include md {
        padding-left: 0;
        margin-bottom: 0;
      }

      &__item {
        font-family: var(--unnamed-font-family-garamond-premier-pro);
        font-weight: 300;
        font-size: 20px;
        line-height: 28px;
        letter-spacing: 0.5px;
        color: var(--dark-grey);
        display: flex;

        @include md {
          font-size: 16px;
          line-height: 22px;
          letter-spacing: 0.4px;
        }

        &::before {
          content: '•';
          color: var(--amber);
          font-size: 20px;
          padding-right: 20px;
        }
      }
    }
  }
}

.partners {
  padding-bottom: 113px;

  .about-us__title {
    margin-bottom: 14px;

    @include md {
      margin-bottom: 40px;
    }
  }

  &__text {
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: 300;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    margin-bottom: 44px;
  }

  &__logos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    flex-wrap: wrap;
    left: -13px;
    max-width: 894px;

    @include md {
      left: 0;
    }

    li {
      @include md {
        width: 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 10px;
      }
    }
  }
}

.team {
  padding-bottom: 60px;

  .about-us__title {
    margin-bottom: 14px;

    @include md {
      margin-bottom: 40px;
    }
  }

  &__section {
    border-top: 1px solid var(--gold);
    padding-top: 18px;
    margin-bottom: 30px;

    &:last-of-type {
      border-bottom: 1px solid var(--gold);
      margin-bottom: 0;
    }

    &__title {
      font-size: 22px;
      color: var(--green-light);
      letter-spacing: 2.2px;
      margin-bottom: 16px;
      text-transform: uppercase;
      text-align: center;

      @include md {
        font-size: 16px;
        letter-spacing: 1.6px;
        padding-left: 0;
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__item {
    width: calc(20% - 30px);
    margin: 0 15px;
    min-width: 158px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 16px;

    @media only screen and (max-width: 600px) {
      width: calc(50% - 40px);
    }

    @include md {
      width: calc(33% - 40px);
    }

    &__img {
      max-width: 143px;
      max-height: 143px;
      width: 40vw;
      height: 40vw;
      border-radius: 50%;
      margin-bottom: 34px;

      &--radius img {
        max-width: 100%;
        max-height: 100%;
        border-radius: 50%;
        border: 2px solid #9c9d9c;
      }

      @include md {
        margin-bottom: 15px;
      }
    }

    &__name {
      font-size: 12px;
      letter-spacing: 1.2px;
      margin-bottom: 6px;
      color: var(--mid-green);
      text-transform: uppercase;
      text-align: center;
      white-space: nowrap;
    }

    &__position {
      color: var(--dark-grey);
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      font-size: 12px;
      letter-spacing: 0.3px;
      white-space: nowrap;
    }
  }
}

.mission {
  padding-top: 75px;
  padding-bottom: 114px;

  @include md {
    padding-top: 45px;
    padding-bottom: 39px;
  }

  .container {
    padding-left: 285px;

    @include md {
      padding-left: 23px;
    }
  }

  .about-us__text {
    max-width: 687px;

    @include md {
      margin-top: 16px;
    }
  }
}

.vision {
  padding-top: 22px;

  .container {
    padding-left: 92px;

    @include md {
      padding-left: 23px;
    }
  }

  .about-us__text {
    max-width: 690px;

    @include md {
      margin-top: 15px;
    }
  }
}

// COMMON

.about-us__title {
  text-transform: uppercase;
  font-size: 28px;
  letter-spacing: 2.8px;
  color: var(--mid-green);
  display: flex;
  align-items: center;
  padding-left: 37px;

  @include md {
    font-size: 16px;
    letter-spacing: 1.6px;
    padding-left: 0;
  }

  &::before {
    content: '';
    display: block;
    background: url('./../assets/title-logo.svg');
    background-size: cover;
    margin-right: 16px;
    position: relative;
    top: 5px;
    height: 72px;
    width: 72px;
    flex-shrink: 0;

    @include md {
      content: '';
      width: 41px;
      margin-right: 9px;
      height: 41px;
      top: -1px;
    }
  }
}

.about-us__text {
  font-family: var(--unnamed-font-family-garamond-premier-pro);
  font-size: 20px;
  line-height: 28px;
  color: var(--dark-grey);
  letter-spacing: 0.5px;

  @include md {
    padding-left: 40px;
  }
}

.section-container {
  padding-left: 126px;
  max-width: 1066px;

  @include md {
    padding-left: 0;
  }
}

.about-us-container {
  background-color: var(--white);
  background-image: url('./../assets/pricing-bg.webp');
  background-position: right -155px top 240px;
  background-size: 876px;
  background-repeat: no-repeat;
  // transition: 1s background-position-y ease-in-out;

  @include md {
    background-size: 517px;
    background-position: right -240px top 50px;
  }
}
